/**
 * 基础信息
 * @type {object}
 */
let CreatedOKLodop7766=null;
const config = Object.assign({},{
    driveURL:['http://localhost:18000/CLodopfuncs.js?priority=0','http://localhost:8000/CLodopfuncs.js?priority=1'],
});
/**
 * @constant 是否能支持CLODOP打印功能
 * @type {boolean} 布尔值
 * @return {boolean}   true是将会使用CLODOP打印,false将会使用LODOP打印
 */
const NEEDCLODOP=(()=>{
    try{
        const UA=navigator.userAgent;
        if (UA.match(/Windows\sPhone/i) !==null) return true;
        if (UA.match(/iPhone|iPod/i) !== null) return true;
        if (UA.match(/Android/i) !== null) return true;
        if (UA.match(/Edge\D?\d+/i) !== null) return true;

        const VERTRIDENT=UA.match(/Trident\D?\d+/i);
        const VERIE=UA.match(/MSIE\D?\d+/i);
        const VEROPR=UA.match(/OPR\D?\d+/i);
        const VERFF=UA.match(/Firefox\D?\d+/i);
        const X64=UA.match(/x64/i);
        if ((VERTRIDENT===null||VERTRIDENT===undefined)&&(VERIE===null||VERIE===undefined)&&(X64!==null)){
            return true;
        } else if ( VERFF !== null) {
            let verFF = VERFF[0].match(/\d+/);
            if ((verFF[0]>= 41)||(X64!==null)) return true;
        } else if ( VEROPR !== null) {
            let verOPR = VEROPR[0].match(/\d+/);
            if ( verOPR[0] >= 32 ) return true;
        } else if ((VERTRIDENT===null||VERTRIDENT===undefined)&&(VERIE===null||VERIE===undefined)) {
            const VERCHROME=UA.match(/Chrome\D?\d+/i);
            if ( VERCHROME !== null ) {
                let verChrome = VERCHROME[0].match(/\d+/);
                if (verChrome[0]>=41) {
                    return true
                }
            }
        }
        return false
    } catch(err) {
        return true
    }
})();
/**
 * 插入LODOP中间件js文件。
 * @param driveURL {string|array|null} 依次解析url，成功一个即返回。
 * @return {Promise}
 */
let buildLODOP = (driveURL)=>{
    return new Promise((resolve,reject)=>{
        let driveURL=driveURL||config.driveURL;
        if(typeof driveURL ==='string'){
            driveURL=[driveURL]
        }
        function getScript(index) {
            if(!driveURL[index]){
                reject(false)
            }else {
                let script=document.createElement('script');
                script.onLoad=function(){
                    resolve(script);
                };
                script.onError=function(){
                    getScript(index+1);
                };
                let head = document.head || document.getElementsByTagName('head')[0] || document.documentElement;
                head.insertBefore(script,head.firstChild);
                script.src=driveURL[index];
            }
        }
        getScript(0);
    })
};
const getLODOP = (oOBJECT,oEMBED)=>{
    const strHtmInstall=`<br><span style="color: #FF00FF">打印控件未安装!点击这里<a href='install_lodop32.exe' target='_self'>执行安装</a>,安装后请刷新页面或重新进入。</span>`;
    const strHtmUpdate=`<br><span style='color: #FF00FF'>打印控件需要升级!点击这里<a href='install_lodop32.exe' target='_self'>执行升级</a>,升级后请重新进入。</span>`;
    const strHtm64_Install=`<br><span style='color: #FF00FF'>打印控件未安装!点击这里<a href='install_lodop64.exe' target='_self'>执行安装</a>,安装后请刷新页面或重新进入。</span>`;
    const strHtm64_Update=`<br><span style='color: #FF00FF'>打印控件需要升级!点击这里<a href='install_lodop64.exe' target='_self'>执行升级</a>,升级后请重新进入。</span>`;
    const strHtmFireFox=`<br><br><span style='color: #FF00FF'>（注意：如曾安装过Lodop旧版附件npActiveXPLugin,请在【工具】->【附加组件】->【扩展】中先卸它）</span>`;
    const strHtmChrome=`<br><br><span style='color: #FF00FF'>(如果此前正常，仅因浏览器升级或重安装而出问题，需重新执行以上安装）</span>`;
    const strCLodopInstall=`<br><span style='color: #FF00FF'>CLodop云打印服务(localhost本地)未安装启动!点击这里<a href='CLodop_Setup_for_Win32NT.exe' target='_self'>执行安装</a>,安装后请刷新页面。</span>`;
    const strCLodopUpdate=`<br><span style='color: #FF00FF'>CLodop云打印服务需升级!点击这里<a href='CLodop_Setup_for_Win32NT.exe' target='_self'>执行升级</a>,升级后请刷新页面。</span>`;
    let LODOP;
    try{
        const ISIE = (navigator.userAgent.indexOf('MSIE')>=0) || (navigator.userAgent.indexOf('Trident')>=0);
        const IS64IE  = ISIE && (navigator.userAgent.indexOf('x64')>=0);
        if (NEEDCLODOP) {
            try{
                /** @namespace window.getCLodop */
                let getCLodop=window.getCLodop||null;
                if(getCLodop){
                    LODOP=getCLodop();
                }
            } catch(err) {

            }
            if (!LODOP && document.readyState!=="complete") {
                alert("C-Lodop没准备好，请稍后再试！"); return;
            }
            if (!LODOP) {
                if (ISIE) {
                    document.write(strCLodopInstall);
                } else{
                    document.body.innerHTML=strCLodopInstall+document.body.innerHTML;
                }
                return
            } else {
                /** @namespace window.CLODOP */
                /** @namespace window.CLODOP.CVERSION */
                if (window.CLODOP.CVERSION<"3.0.3.7") {
                    if (ISIE){
                        document.write(strCLodopUpdate);
                    } else{
                        document.body.innerHTML=strCLodopUpdate+document.body.innerHTML;
                    }
                }
                if (oEMBED && oEMBED.parentNode) {
                    oEMBED.parentNode.removeChild(oEMBED);
                }
                if (oOBJECT && oOBJECT.parentNode) {
                    oOBJECT.parentNode.removeChild(oOBJECT);
                }
            }
        } else {
            //=====如果页面有Lodop就直接使用，没有则新建:==========
            if ((oOBJECT!==undefined&&oOBJECT!==null) || (oEMBED!==undefined&&oEMBED!==null)) {
                if (ISIE) {
                    LODOP=oOBJECT;
                } else  {
                    LODOP=oEMBED;
                }
            } else if (CreatedOKLodop7766===null){
                LODOP=document.createElement('object');
                LODOP.setAttribute('width','0');
                LODOP.setAttribute('height','0');
                LODOP.setAttribute('style','position:absolute;left:0px;top:-100px;width:0px;height:0px;');
                if (ISIE) {
                    LODOP.setAttribute('classid','clsid:2105C259-1E0C-4534-8141-A753534CB4CA');
                } else {
                    LODOP.setAttribute('type','application/x-print-lodop');
                }
                document.documentElement.appendChild(LODOP);
                CreatedOKLodop7766=LODOP;
            } else {
                LODOP=CreatedOKLodop7766;
            }
            //=====Lodop插件未安装时提示下载地址:==========
            if ((LODOP===null||LODOP===undefined)||(typeof(LODOP.VERSION)==='undefined')) {
                if (navigator.userAgent.indexOf('Chrome')>=0){
                    document.body.innerHTML=strHtmChrome+document.body.innerHTML;
                }
                if (navigator.userAgent.indexOf('Firefox')>=0){
                    document.body.innerHTML=strHtmFireFox+document.body.innerHTML;
                }
                if (IS64IE) {
                    document.write(strHtm64_Install);
                } else if (ISIE) {
                    document.write(strHtmInstall);
                } else {
                    document.body.innerHTML=strHtmInstall+document.body.innerHTML;
                }
                return LODOP;
            }
        }
        /** @namespace LODOP.VERSION */
        if (LODOP.VERSION<'6.2.2.1') {
            if (!NEEDCLODOP){
                if (IS64IE) {
                    document.write(strHtm64_Update);
                } else if (ISIE) {
                    document.write(strHtmUpdate);
                } else{
                    document.body.innerHTML=strHtmUpdate+document.body.innerHTML;
                }
            }
            return LODOP;
        }
        return LODOP;
    } catch(err) {
        alert('getLodop出错:'+err);
    }
};
class Print {
    static LODOP=null;
    constructor(option){
        const noop=()=>{};
        this.option=Object.assign(config,{
            beforeBuild:noop,
            afterBuild:noop,
            errorBuild:noop,
            mounted:noop,
            renderTemplate:noop
        },option);
        if (!Print.LODOP) {
            this.option.beforeBuild.call(this);
            buildLODOP(null).then(() => {
                Print.LODOP = this.LODOP = getLODOP();
                this.option.afterBuild.call(this);
                this.option.mounted.call(this);
            }).catch(err => {
                this.option.errorBuild.call(this,err);
            });
        } else {
            this.option.mounted.call(this);
        }
    }
    push(){

    }
    registerTemplate(template){
        this.templates=[];
    }
    static renderTemplate(template){
        this.option.renderTemplate(template)
    }
}
export default Print



